footer {
    margin-top: -40px;
    padding: 30px 114px;
    border-top: 1px solid rgba(72, 72, 72, .2);
    display: flex;
    justify-content: space-between;
    align-items: center;

    a:nth-of-type(2) {
        @include firaSans15;
        font-size: 12px;
        opacity: .5;
        text-decoration: none;
    }

    .footer__socials {
        width: 120px;
        height: 25px;
        display: flex;
        justify-content: space-between;

        a {
            display: block;
            width: 22px;
            height: 22px;
            background-image: url(../../img/sm.svg);

            &:nth-of-type(1) {
                background-position: 0px;
            }

            &:nth-of-type(2) {
                background-position: 24px;
            }

            &:nth-of-type(3) {
                background-position: 65px;
            }
        }

    }
}
